@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;1,300;1,400;1,500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root{
  --pink:#FF046F;
  --pink2:#d54e89;
  --violet:#C63BFA;
}

*{
  font-family: 'Roboto', sans-serif;
  text-transform: capitalize;
  text-decoration: none;
  outline: none;
  margin: 0; padding: 0;
  box-sizing: border-box;
}

*::selection{
  background:#FF046F;
  background:var(--pink);
  color:#fff;
}

html{
  font-size: 62.5%;
  overflow-x: hidden;
}

.heading{
  font-size: 4rem;
  /* color:var(--pink); */
  color:black;
  text-align: center;
  padding:0 1rem;
  padding-top: 6rem;
  letter-spacing: .2rem;
  font-weight: 500;
}

.title{
  padding:0 1rem;
  font-size: 2rem;
  text-align: center;
  font-weight: 400;
  color:#aaa;
}
/* 
body {
    background: #333;
} */

header{
  width:96%;
  background:#fff;
  position: fixed;
  top:2rem; left:50%;
  transform: translateX(-50%);
  border-radius: 5rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding:1rem 2rem;
  z-index: 1000;
  transition: .2s;
}

.header-active{
  top:0;
  width: 100%;
  border-radius: 0;
  box-shadow: 0 .1rem .3rem rgba(0,0,0,.3);
}

header .navbar ul{
  display: flex;
  align-items: center;
  justify-content: space-between;
  list-style: none;
}

header .navbar ul li{
  margin:0 1rem;
}

header .navbar ul li a{
  font-size: 2rem;
  /* color:var(--pink); */
  color:black;
  transition: .2s;
}

header .navbar ul li .active,
header .navbar ul li a:hover{
  /* color:var(--violet); */
  color:#FF046F;
  color:var(--pink);
}

header .logo{
  font-size: 2.5rem;
  /* color: var(--violet); */
  color:black;
  text-transform: uppercase;
}

header .logo i{
  color:#FF046F;
  color:var(--pink);
  padding:0 .2rem;
}

header .fa-bars{
  font-size: 3rem;
  /* color:var(--pink); */
  color:black;
  cursor: pointer;
  display: none;
  transition: .2s;
}

.home{
  min-height: 100vh;
  /* background:linear-gradient(rgba(198, 59, 250,.8),rgba(198, 59, 250,.8)), url(http://localhost:9000/bg.jpg) no-repeat; */
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  z-index: 0;
  overflow:hidden;
  position: relative;
}

.home .content{
  display: flex;
  align-items: center;
  justify-content: center;
  flex-flow: column;
  padding-top: 14rem;
  padding-bottom: 8rem;
  text-align: center;
}

.home .content h1{
  font-size: 5rem;
  color: #fff;
  padding:0 1rem;
  text-shadow: 0 .3rem .5rem rgba(0,0,0,.3);
  text-transform: uppercase;
}

.home .content p{
  font-size: 2rem;
  color:#eee;
  padding:1rem 25rem;
}


.home .content button{
  height:4rem;
  width:20rem;
  border-radius: 5rem;
  background:#FF046F;
  background:var(--pink);
  color:#fff;
  font-size: 2rem;
  cursor: pointer;
  transition: .2s;
  border:none;
  box-shadow: 0 .3rem 1rem rgba(0,0,0,.3);
}

.home .content button:hover{
  letter-spacing: .2rem;
}

.home .box-container{
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.home .box-container .box{
  height:20rem;
  width:25rem;
  background:#fff;
  text-align: center;
  border-radius: 1rem;
  box-shadow: 0 .3rem .5rem rgba(0,0,0,.5);
  margin: 2rem;
  cursor: pointer;
}

.home .box-container .box i{
  height:6rem;
  width:6rem;
  line-height: 6rem;
  text-align: center;
  border-radius: 50%;
  color:#fff;
  /* background:var(--violet); */
  background: black;
  font-size: 3rem;
  margin:2rem 0;
  transition: .2s;
}

.home .box-container .box h3{
  font-size: 2rem;
  /* color:var(--pink); */
  color:black;
  transition: .2s;
}

.home .box-container .box p{
  font-size: 1.3rem;
  padding:.7rem 2rem;
  color:#666;
}

.home .box-container .box:hover i{
  background:#FF046F;
  background:var(--pink);
  /* background:black; */
}

.home .box-container .box:hover h3{
  color:#C63BFA;
  color:var(--violet);
}

.home::before{
  content: '';
  position: absolute;
  bottom:-25rem; left:50%;
  transform: translateX(-50%);
  border-top: 70vh solid #fff;
  width:120%;
  border-radius: 50%;
  z-index: -1;
}

.about .row{
  display: flex;
  justify-content: center;
  align-items: center;
}

.about .row .image img{
  height:70vh;
  width:50vw;
}

.about .row .content{
  padding-left: 5rem;
}

.about .row .content h3{
  font-size: 3rem;
  /* color:var(--pink); */
  color:black;
}

.about .row .content p{
  font-size: 1.6rem;
  color:#999;
  padding:1rem 0;
}

.about .row .content button{
  height:3.5rem;
  width:17rem;
  /* background:var(--violet); */
  background:#FF046F;
  background:var(--pink);
  color:#fff;
  border:none;
  border-radius: 5rem;
  box-shadow: 0 .3rem 1rem rgba(0,0,0,.3);
  cursor: pointer;
  font-size: 2rem;
  transition: .2s;
}

.about .row .content button:hover{
  letter-spacing: .2rem;
  opacity: .8;
}

.teacher{
  background:#f9f9f9;
}

.teacher .card-container{
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  padding:2rem 0;
}

.teacher .card-container .card{
  padding:2rem;
  margin: 2rem;
  background:#fff;
  text-align: center;
  width:28rem;
  box-shadow: 0 .3rem .5rem rgba(0,0,0,.3);
}

.teacher .card-container .card img{
  height:30rem;
  width:100%;
  object-fit: cover;
  -webkit-filter: grayscale();
          filter: grayscale();
  transition: .2s;
}

.teacher .card-container .card:hover img{
  -webkit-filter: grayscale(0);
          filter: grayscale(0);
}

.teacher .card-container .card h3{
  color:#FF046F;
  color:var(--pink);
  color:black;
  font-size: 2rem;
  padding:.5rem 0;
}

.teacher .card-container .card p{
  font-size: 1.7rem;
  color:#666;
}

.teacher .card-container .card .icons a{
  font-size: 1.9rem;
  padding:1.5rem .5rem;
  color:#FF046F;
  color:var(--pink);
}

.teacher .card-container .card .icons a:hover{
  color:#C63BFA;
  color:var(--violet);
}

.course .box-container{
  width:85%;
  display: flex;
  justify-content: center;
  margin:0 auto;
  padding-top: 4rem;
  flex-wrap: wrap;
}

.box-container .box button{
  height:3rem;
  width:20rem;
  border-radius: 5rem;
  background:#FF046F;
  background:var(--pink);
  color:#fff;
  font-size: 1.5rem;
  cursor: pointer;
  transition: .2s;
  border:none;
  box-shadow: 0 .3rem 1rem rgba(0,0,0,.3);
}

.box-container .box button:hover, .see-more:hover {
  letter-spacing: .2rem;
}

.see-more {
  height:3rem;
  width:20rem;
  border-radius: 5rem;
  background:#FF046F;
  background:var(--pink);
  color:#fff;
  font-size: 1.5rem;
  cursor: pointer;
  transition: .2s;
  border:none;
  box-shadow: 0 .3rem 1rem rgba(0,0,0,.3);

  width:65%;
  display: flex;
  justify-content: center;
  margin:0 auto;
  padding-top: 0.5rem;
  /* flex-wrap: wrap; */
  margin-bottom: 50px;
}

.course .box-container .box{
  height:20rem;
  width:25rem;
  margin:3.5rem 2rem;
  text-align: center;
  border-radius: 1rem;
  box-shadow: .3rem .3rem 0 .1rem rgb(170, 170, 170),
              .5rem .5rem .5rem rgba(0,0,0,.3);
}

.course .box-container .box i{
  height:10rem;
  width:10rem;
  line-height: 8rem;
  text-align: center;
  color:#fff;
  border-radius: 50%;
  background:#FF046F;
  background:var(--pink);
  /* background:black; */
  margin-top: -4rem;
  border:1rem solid #fff;
  border-left:1rem solid #FF046F;
  border-left:1rem solid var(--pink);
  /* border-left:1rem solid black; */
  border-right:1rem solid #FF046F;
  border-right:1rem solid var(--pink);
  /* border-right:1rem solid black; */
  box-shadow: 0 0 0 1rem #fff inset;
  font-size: 3.5rem;
}

.course .box-container .box h3{
  font-size: 2rem;
  /* color:var(--violet); */
  color:black;
}

.course .box-container .box p{
  font-size: 1.5rem;
  /* color:#999; */
  color:black;
  padding:1rem 0;
}

.review{
  background:#f9f9f9;
}

.review .box-container{
  display: flex;
  justify-content: center;
  padding-top: 4rem;
  flex-wrap: wrap;
}

.review .box-container .box{
  width:28rem;
  padding-bottom: 1rem;
  text-align: center;
  box-shadow: 0 .3rem .5rem rgba(0,0,0,.3);
  background:#fff;
  margin:4rem 2rem;
}

.review .box-container .box img{
  height:8.5rem;
  width:8.5rem;
  object-fit: cover;
  border-radius: 50%;
  margin-top: -4rem;
  border:.6rem solid #FF046F;
  border:.6rem solid var(--pink);
  /* border-left:.6rem solid var(--violet); */
  border-left:.6rem solid #FF046F;
  border-left:.6rem solid var(--pink);
  /* border-bottom:.6rem solid var(--violet); */
  border-bottom:.6rem solid #FF046F;
  border-bottom:.6rem solid var(--pink);
}

.review .box-container .box h3{
  font-size: 2rem;
  /* color:var(--pink); */
  color:black;
  padding:0 .5rem;
}

.review .box-container .box p{
  font-size: 1.4rem;
  color:#999;
  padding:0 1.3rem;
}

.review .box-container .box .stars i{
  font-size: 1.6rem;
  /* color:var(--violet); */
  color:#FF046F;
  color:var(--pink);
  padding:1.3rem .1rem;
}

.contact{
  background:#333;
}

.contact .row{
  display: flex;
  align-items: center;
  justify-content: center;
  padding:5rem 0;
}

.contact .row .image img{
  width: 50vw;
  height:70vh;
}

.contact .row .form-container{
  width:50%;
  padding-right: 6rem;
}

.contact .row .form-container input, textarea {
  height:4rem;
  width:100%;
  background:#fff;
  border:none;
  border-radius: 5rem;
  padding:0 1rem;
  margin:1rem 0;
  color:#FF046F;
  color:var(--pink);
  font-size: 1.7rem;
  transition: .2s linear;
}

.search-course {
  width:40%;
  display: flex;
  justify-content: center;
  margin:0 auto;
  padding-top: 0.5rem;
  margin-bottom: 50px;
  border-radius: 5rem;
  outline:none;
  height:4rem;
  border:1px solid;
  padding:0 1rem;
  margin-top: 50px;
  margin-bottom: 20px;  
}

::-webkit-input-placeholder { /* WebKit browsers */
  text-transform: none;
}
:-ms-input-placeholder { /* Internet Explorer 10+ */
  text-transform: none;
}
::placeholder { /* Recent browsers */
  text-transform: none;
}
input[type="email"],textarea {
  text-transform: none;
}

.contact .row .form-container input:focus, textarea:focus{
  border:.2rem solid #FF046F;
  border:.2rem solid var(--pink);
}

.contact .row .form-container textarea{
  height:20rem;
  border-radius: 1rem;
  padding:1rem; 
  resize: none;
}

.contact .row .form-container input[type="submit"]{
  width:17rem;
  background:#FF046F;
  background:var(--pink);
  color:#fff;
  cursor: pointer;
  font-size: 2rem;
  transition: .2s;
}

.contact .row .form-container input[type="submit"]:hover{
  background:none;
  color:#FF046F;
  color:var(--pink);
  border:.2rem solid #FF046F;
  border:.2rem solid var(--pink);
}

.footer{
  text-align: center;
}

.footer .icons{
  padding:3rem 0;
}

.footer .icons a{
  height:4.5rem;
  width: 4.5rem;
  line-height: 4.2rem;
  text-align: center;
  border-radius: 50%;
  /* color:var(--violet);
  border:.2rem solid var(--violet); */
  color:black;
  border:.1rem solid black;
  font-size: 2rem;
  margin:0 .1rem;
  transition: .2s linear;
}

.footer .icons a:hover{
  color:#fff;
  /* background:var(--pink);
  border-color: var(--pink); */
  background:black;
  border-color: black;

}


.footer .credit{
  font-size: 2rem;
  border-top: .1rem solid #999;
  margin:0 auto;
  margin-bottom: 2rem;
  color:#666;
  width:90%;
  padding:1rem 0;
}

.footer .credit span{
  color:#FF046F;
  color:var(--pink);
}

header .fa-times{
  transform: rotate(180deg);
  /* color:var(--violet); */
  color:#FF046F;
  color:var(--pink);
}

.description {
  font-size: 1.75rem;
  color:black;
  padding:1rem 0;
} 

@media(min-width: 1200px) {
  .videoWrapper {
      margin-left: 25%;
  }
}



@media screen and (min-width : 992px) and  (max-width : 1199px) {
  .videoWrapper {
    margin-left: 22%;
}
}

@media(max-width: 991px) {
  .videoWrapper {
    position: relative;
    padding-bottom: 56.25%; 
    height: 0;
  }
  .videoWrapper iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
  
}



.container {
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}
@media (min-width: 768px) {
  .container {
    width: 750px;
  }
}
@media (min-width: 992px) {
  .container {
    width: 970px;
  }
}
@media (min-width: 1200px) {
  .container {
    width: 1170px;
  }
}

/* media queries  */

@media (max-width:768px){

  html{
    font-size: 55%;
  }

  header .fa-bars{
    display: block;
  }

  header .navbar{
    position: fixed;
    top:-100rem; left:0;
    width:100%;
    background:#fff;
    border-radius: 1rem;
    opacity: 0;
    transition: .2s linear;
  }

  header .navbar ul{
    flex-flow: column;
    padding:2rem 0;
  }

  header .navbar ul li{
    margin:1rem 0;
    width:100%;
    text-align: center;
  }

  header .navbar ul li a{
    font-size: 3rem;
    display: block;
  }

  header .nav-toggle{
    top:5.5rem;
    opacity: 1;
  }

  .home .content p{
    padding:1.5rem 2rem;
  }

  .home::before{
    display: none;
  }

  .about .row{
    flex-flow: column-reverse;
  }

  .about .row .image img{
    height:60vh;
    width:90vw;
  }

  .about .row .content{
    padding:0 2.5rem;
  }

  .course .box-container{
    width:100%;
  }

  .contact .row{
    flex-flow: column;
  }

  .contact .row .image img{
    height:50vh;
    width:90vw;
  }

  .contact .row .form-container{
    width:90%;
    padding:0;
    text-transform: none;
  }
}

.Toastify__toast, .Toastify__toast--error {
